:root {
  --peach: #F0A685; 
  --light-blue: #8DCFE7; 
  --dark-blue: #375B6B; 
  --darker-blue: #304856; 
  --grey-blue: #30343F; 
  --grey: #445561; 
  --bright-blue: #119DA4; 
  --yellow: yellow; 
  --red: #D7263D;
  --green: #22CE6B; 
}
body {
  background-color: var(--grey-blue);
}
input {
  font-family: "Avenir Next";
  font-weight: 600; 
  color: white; 
  background-color: var(--darker-blue);
  border: none; 
  border-radius: 10px; 
  outline: none;
}
button {
  font-family: "Avenir Next";
  font-size: 12px; 
  font-weight: bold;
  border: none; 
  border-radius: 2px; 
  outline: none;
}
a {
  color: white; 
}
a:hover {
  color: var(--light-blue);
}

.App {
  font-family: "Avenir Next";
  font-weight: 600;
} 
#home {
  min-height: 100vh;
  color: white; 
  position: relative; 
  text-align: center;
}
.Header {
  display: flex; 
  justify-content: space-between;
  position: sticky; 
  top: 0; 
  height: 36px; 
  width: 100vw; 
  padding: 20px 0px; 
  background-color: var(--grey-blue);
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.10); 
  z-index: 2;
}
.Corner {
  display: flex; 
  padding-right: 20px; 
}
.Corner-buttons {
  display: flex; 
  justify-content: space-between;
  width: 240px; 
  padding-left: 25px; 
}
.prepfactory {
  font-size: 28px; 
  margin: 0px; 
  padding-left: 40px; 
  margin-top: -1px; 
}
.Header-menu {
  font-size: 12px; 
  padding: 11px 20px; 
  color: white; 
  text-decoration: none;
}
.Header-menu:hover {
  color: var(--yellow); 
}
.Start-button {
  padding: 10px; 
  border-radius: 5px; 
  height: 36px; 
  width: 110px;
  color: white; 
  font-size: 12px; 
  transition: all .3s;
  position: relative; 
  background-color: transparent;
}
.Start-button:after {
  content: '';
  position: absolute;
  left: 0; 
  bottom: 0; 
  border-radius: 5px; 
  height: 36px; 
  width: 110px;
  z-index: -2;
  background-color: var(--color);
}
.Start-button:before {
  content: '';
  position: absolute;
  left: 0; 
  bottom: 0; 
  border-radius: 5px; 
  height: 36px; 
  width: 0px;
  background-color: var(--darker);
  transition: all .3s;
  z-index: -1;
}
.Start-button:hover::before {
  width: 110px; 
}
.Arrow-div {
  position: absolute;
  top: 100vh;
  right: 20px;
  bottom: 0;
  pointer-events: none;
}
.Arrow {
  background-color: var(--dark-blue);
  color: var(--grey-blue); 
  font-size: 45px; 
  text-decoration: none;
  border-radius: 50%; 
  line-height: 70px; 
  width: 70px; 
  text-align: center; 
  display: inline-block; 
  position: sticky;
  pointer-events: all; 
  top: calc(100vh - 5rem);
  z-index: 2; 
}
.Arrow:hover {
  background-color: var(--light-blue);
  color: var(--yellow); 
}
.Content {
  display: flex; 
  flex-direction: column;
  justify-content: center;
  width: 100%; 
}
.Section {
  height: 100vh; 
  z-index: 1; 
  text-align: center;
  font-size: 30px; 
  display: flex; 
  flex-direction: column;
  overflow: auto;
  position: relative; 
  justify-content: center;
}
.Section::before {
  content: "";
  position: absolute; 
  display: block; 
  left: 0;
  right: 0;
  z-index: -1;
  height: 100%;
  background-image: var(--image);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(60%);
}

.Head-text {
  font-size: 48px; 
}

.Testimonial {
  position: absolute; 
  width: 1100px; 
  left: 0;
  right: 0;
  margin-left: auto; 
  margin-right: auto; 
}
.Testimonial-teacher {
  position: relative; 
  width: 750px; 
  left: 0;
  right: 0;
}
.Quote-box {
  display: flex; 
  flex-direction: column;
  justify-content: center;
  border-radius: 20px; 
  border: 1px solid var(--light-blue); 
  background-color: var(--grey-blue);
  color: white; 
  padding: 50px 125px; 
  height: 500px; 
}
.Arrow-icon {
  position: absolute; 
  color: var(--light-blue);
  font-size: 60px; 
  line-height: 60px; 
  width: 60px; 
  border-radius: 50%; 
  background-color: var(--dark-blue);
  left: 0;
  right: 0;
  margin: 0px auto; 
  z-index: 1; 
}
.Arrow-icon:hover {
  color: var(--yellow);
}
.left {
  margin-left: 30px; 
  padding: 4px 6px 2px 0px; 
}
.right {
  margin-right: 30px; 
  padding: 4px 0px 2px 6px; 
}
.Testimonial-head {
  position: absolute; 
  top: 45px; 
  margin-left: -10px; 
  font-size: 30px; 
}
.Quote-div {
  margin-top: -2em; 
  text-align: left;
  font-size: 30px; 
}
.Quote-icon {
  margin-bottom: -1.6em; 
  color: var(--yellow); 
}
.Quote {
  font-size: 27px; 
  padding-left: 50px; 
}
.Quote-name-div {
  position: absolute; 
  left: 0;
  right: 0;
  margin: 0px auto; 
  bottom: 3.2em; 
}
.Quote-name {
  font-size: 20px; 
  color: var(--yellow); 
  margin: 5px; 
}
.Quote-place {
  font-size: 16px; 
  color: var(--light-blue);
  margin: 0px; 
}
.Dot-div {
  position: absolute; 
  left: 0;
  right: 0;
  margin: 0px auto; 
  bottom: 40px; 
}
.Quote-dot {
  border-radius: 50%; 
  width: 15px; 
  height: 15px; 
  display: inline-block; 
  margin: 25px 7px 0px 7px; 
}

.Inner-box-div {
  display: flex; 
  justify-content: space-between;
  padding: 0px; 
}

.Explanation {
  text-align: left; 
  font-size: 20px; 
  width: 30%; 
  display: flex; 
  flex-direction: column;
  justify-content: center;
}
.Example-pic {
  max-width: 65%; 
  max-height: 100%;
}

.Teacher-section {
  width: 1100px; 
  margin: 0 auto; 
}
.Teacher-sect-div {
  display: flex; 
  flex-direction: row;
  justify-content: space-between;
}
.School-license {
  display: flex; 
  flex-direction: column;
  justify-content: center;
  font-size: 16px; 
  width: 270px; 
  text-align: left; 
  color: white; 
}
.School-license-heading {
  color: var(--yellow);
  font-size: 18px;
  margin: -4px 0 4px 0; 
}
.Request-form {
  color: var(--light-blue);
  font-size: 12px;
  text-align: left;
  margin-top: 6px; 
}
.Request-input {
  font-family: "Avenir Next";
  font-size: 14px; 
  font-weight: 600; 
  color: white; 
  text-align: left; 
  
  background-color: var(--darker-blue);
  border: none; 
  border-radius: 10px; 
  width: 250px; 
  height: 25px; 
  padding: 10px;
  margin: 2px 0px 5px 0px; 
}
.Request-button {
  font-size: 14px; 
  color: white; 
  background-color: var(--darker-blue);
  border-radius: 5px; 
  width: 270px; 
  height: 45px; 
  margin-top: 10px; 
}
.Request-button:hover {
  background-color: var(--light-blue);
  color: var(--darker-blue);
}

.Copyright-div {
  position: absolute; 
  bottom: 0; 
  width: 100%; 
  text-align: left; 
}
.End-break {
  margin: 0 110px; 
  border-color: var(--light-blue);
}
.Footer-text {
  font-size: 12px; 
  padding: 2em 0px; 
  display: flex; 
  flex-direction: row;
  justify-content: space-between;
}
.Email {
  margin: 1em 0; 
  padding-left: 110px; 
  color: white; 
  text-decoration: none;
}
.Email:hover {
  color: var(--light-blue);
}
.Copyright {
  margin: 1em 0; 
  padding-right: 110px; 
}

.Corporate {
  display: flex; 
  justify-content: space-between;
  margin: 0 110px; 
  font-size: 36px; 
}
.Buy {
  width: 430px; 
  text-align: left; 
  padding-left: 100px; 
  font-size: 13px; 
}
.Buy-heading {
  font-size: 24px; 
  margin-bottom: 0px; 
}
.Cost-div {
  padding: 10px 0px 10px 150px; 
}
.Dollar {
  display: inline-block; 
  margin: 2px 5px 0 0; 
  font-size: 40px; 
  color: var(--yellow); 
}
.Cost {
  position: absolute; 
  display: inline-block; 
  font-size: 18px; 
  color: var(--light-blue);
}
.Buy-text {
  font-size: 14px;
}

.FAQ {
  display: flex; 
  flex-direction: column;
  font-size: 14px; 
  text-align: left;
  height: 350px; 
  margin-top: 10px; 
  color: var(--light-blue);
}
.FAQ-text {
  margin: 0; 
}
.Drop-down {
  margin: 2px 0px; 
}
.Drop-down-box {
  display: inline-block; 
  padding: 10px 30px; 
  width: 350px; 
  line-height: 1.2em; 
  color: white; 
  background-color: var(--dark-blue); 
  border-radius: 5px;
  border-bottom: 1px solid var(--grey-blue); 
}
.Drop-down-text {
  margin: 0.5em 0; 
  display: flex; 
  justify-content: space-between;
}
.Plus-minus {
  text-align: center;
  height: 1.4em; 
  line-height: 1.4em;
  width: 1.4em; 
  font-size: 1.4em; 
  padding: 1px 0px 0px 1px; 
  margin: -.3em 0 -1em 0; 
  color: white; 
  background-color: var(--grey-blue);
  border-radius: 50%;
}
.Stripe {
  height: 100%; 
  display: flex; 
  padding-right: 30px; 
  padding-top: 30px; 
}
.Stripe-box {
  height: 440px; 
  width: 460px; 
  padding: 10px; 
  border-radius: 10px;
  background-color: var(--dark-blue); 
  display: flex; 
  flex-direction: column;
  justify-content: center;
  font-size: 20px; 
}

html {
  scroll-behavior: smooth;
}
